// Navigator.tsx

import React, { FC, useState, useEffect } from 'react';
import SideBar from './SideBar';
import './Navigator.css';

interface NavigatorProps  {
  onLinkClicked: (item: SidebarItem, practiceId: number) => void;
  onHomeClicked: (lesson: SidebarItem) => void;
  TitleText: string;
  param?: string;
  sidebarItems: SidebarItem[];
}

interface SidebarItem {
  label: string;
  link: string;
  nestedList?: SidebarItem[];
}

export const Navigator: FC<NavigatorProps > = ({ onLinkClicked, onHomeClicked, TitleText, param, sidebarItems }) => {
  const [isSideBarOpen, setIsSideBarOpen] = useState<boolean>(false); // サイドバーの開閉状態を管理する状態
  
  // リンクがクリックされたときの処理
  const handleLinkClick = (item: SidebarItem, practiceId:number) => {
    onLinkClicked(item,practiceId);  // 親へ通知
    console.log(`Link clicked in Navigator: ${item.label}`);
  };

  // ホームボタンがクリックされたときの処理
  const handleHomeClick = (item: SidebarItem) => {
    onHomeClicked(item);  // 親へ通知
    console.log(`handleHomeClick in Navigator:`);
    setIsSideBarOpen(true);
  };

  // トグルがクリックされたときの処理
  const handleToggleClick = () => {
    setIsSideBarOpen(!isSideBarOpen);
  };

  // paramから選択授業回と演習を取得
  let selectLesson: SidebarItem = { label: 'NO_LESSON', link: '0-0' };
  let selectPractice: SidebarItem = { label: 'NO_PRACTICE', link: '0-0' };
  
  let lessonId: number = 0;
  let practiceId: number = 0;
  if (param !== undefined) {

    const parts = param.split('-');
    
    // 分割した結果が2つ以上の場合、数値に変換して変数に代入
    if (parts.length >= 2) {
      lessonId = parseInt(parts[0], 10);
      practiceId = parseInt(parts[1], 10);
      console.log(`lessonId : practiceId parts in Programming: ${lessonId} : ${practiceId}`);
    }

    // 変換が成功した場合の処理
    selectLesson = sidebarItems[lessonId];
    if (selectLesson.nestedList !== undefined) selectPractice = selectLesson.nestedList[practiceId];
  }
  
  console.log(`lesson, practice in Programming: ${selectLesson.label}, ${selectPractice.label}`);

  // サイドバー項目
  let sidebarList: SidebarItem[] = [];
  if( practiceId > 0 && selectLesson && selectLesson.nestedList )
  {
    sidebarList = selectLesson.nestedList;
  } else {
    sidebarList = sidebarItems;
  }

  return (
    <div>

      {/* 半透明のボックス */}
      {/*<div className="overlay-box"></div>*/}

      {/* トグルボタン */}
      <button className="toggle-button" onClick={() => handleToggleClick()} >
          {isSideBarOpen ? '×' : '□'}
      </button>
      
      {/* サイドバーコンポーネント */}
      <SideBar onLinkClicked={handleLinkClick} isOpen={isSideBarOpen} selectLesson={selectLesson} dispList={sidebarList} practiceId={practiceId} />

      {/* 授業名 */}
      <span className="Title-text">{/*TitleText*/}</span>

      {/* ホームボタン */}
      <button className="button-home" onClick={() => handleHomeClick(selectLesson)}>🏠</button>

      {/* 回 */}
      <div className="Title-Lesson-text">
        {/*selectedLesson?.label*/}
      </div>
      {/* 演習 */}
      <div className="Title-Practice-text">
        {/*selectedPractice?.label*/}
      </div>

    </div>
  );
};
