// Programming.tsx

import React, { FC, useState, useEffect } from 'react';
import { Navigator } from '../Navigator';
import ContentFrame from '../ContentFrame';
import { useParams, useNavigate } from 'react-router-dom';

const strTitleText = "情報処理Ⅱ";
const LessonPrefix = 'programming';

// サイドバーの項目データ
const sidebarData: SidebarItem[] = [
  { label: 'TOP', link: '0-0' },
  {
    label: '第１回：Processing入門',
    link: '1-0',
    nestedList: [
  //    { id: 0, label: 'TOP', link:'1-0'},
      { label: 'Processing導入', link: '1-1' },
      { label: '最初のサンプルプログラム', link: '1-2' },
      { label: '演習１：プログラムをコピーして実行する．', link: '1-3' }
    ]
  },
  {
    label: '第２回：図形描画',
    link: '2-0',
    nestedList: [
//      { id: 0, label: 'TOP', link:'2-0'},
      { label: 'Processing再入門', link: '2-1' },
      { label: '連続した線による表現', link: '2-2' },
      { label: '演習１', link: '2-3' }
    ]
  },
  { label: '第３回：演算と変数', link: '3-0' },
  { label: '第４回：制御文：繰り返し', link: '4-0' },
  { label: '第５回：制御文：条件分岐＋配列', link: '5-0' },
  { label: '第６回：アニメーション', link: '6-0' },
  { label: '第７回：関数の作成', link: '7-0' },
  { label: '第８回：作業回', link: '8-0' },
  { label: '第９回：再帰関数数', link: '9-0' },
  { label: '第１０回：プログラミング環境', link: '10-0' },
  { label: '第１１回：作業回数', link: '11-0' },
  { label: '第１２回：クラス', link: '12-0' },
  { label: '第１３回：講評＆授業総括', link: '13-0' }
];

interface SidebarItem {
  label: string;
  link: string;
  nestedList?: SidebarItem[];
}

const Programming: FC = () => {
  const { param } = useParams();
  const navigate = useNavigate(); // useNavigateフックを利用

  // リンクがクリックされたときの処理
  const handleLinkClick = (item: SidebarItem, practiceId: number) => {
    
    if( practiceId === 0 )
    { // 授業回をクリック
      if( item.nestedList !== undefined )
      {
        navigate(`/programming/${item.nestedList[0].link}`);
      }  
    } else {
      // 演習をクリック
      navigate(`/programming/${item.link}`);
    }
    console.log(`Link clicked in Programming: ${practiceId}`);
  };

  // ホームがクリックされたときの処理
  const handleHomeClick = (lesson: SidebarItem) => {

    if( lesson.nestedList !== undefined )
    {
      console.log(`Home clicked in Programming: ${lesson.label}`);
      navigate(`/programming/${lesson.link}`);
    }  
  };

  return (
    <div className="Programming">

      {/* コンテンツ */}
      <div className="main-content">
        <ContentFrame LessonPrefix={LessonPrefix} param={param} />
      </div>
      {/* Navigatorコンポーネント */}
        <Navigator onLinkClicked={handleLinkClick} onHomeClicked={handleHomeClick} TitleText={strTitleText} param={param} sidebarItems={sidebarData} />
    </div>
  );
};

export default Programming;
